import React, {useState, useEffect} from 'react';
import './LoginRegister.css'
import { authService } from '../tools/fbase'
import { firebaseInstance } from '../tools/fbase'
import { dbService } from '../tools/fbase'

function LoginPage({history}) {
    const [id, setId] = useState("");
    const [password, setPassword] = useState("");
    const [done, setDone] = useState(false);

    const socialLogin = async (e) => {
        const { 
            target : { name }, 
        } = e;
        let provider;
        if (name === "googleLogin"){
            provider = new firebaseInstance.auth.GoogleAuthProvider();
        }else{
            console.log("   .");
        }

        const data = await authService.signInWithPopup(provider);
        await checkRegister(data.user.multiFactor.user.email);
        history.push('/');
        history.go();
    }

    const submit = async (e) => {
        var data = '';
        e.preventDefault();
        try{
            data = await authService.signInWithEmailAndPassword(
                id, password
            )
            checkRegister(data.user.multiFactor.user.email)
        } catch (err){
            console.log(err)
        }
        console.log(data)
    }

    const checkRegister = async (email) => {
        const usersCollections = await dbService
            .collection("users")
            .where("email", "==", email)
            .get();
        
        const usersExist = usersCollections.docs.map(doc => {
            return({...doc.data(), id: doc.id})
        })

        console.log("  ", email, usersExist)


        if(usersExist.length === 0){
            //       
            console.log("  .");
            setDone(true);
        }else{
            localStorage.setItem("name", usersExist[0].name);
            localStorage.setItem("job", usersExist[0].job);

            history.push('/');
            history.go();
        }
    }

    return (
        <>
        {done ? 
        <div>
            Initializing... 
            {/*        ..? */}
        </div>
        :
        <div className="login-container">
            <div className="login__inner">
                <div>
                        .
                </div>
                <form onSubmit={e => submit(e)}>
                    <input 
                        type="text" 
                        placeholder=" ." 
                        required
                        value={id}   
                        onChange={e => setId(e.currentTarget.value)}
                    />
                    <input  
                        type="password" 
                        placeholder=" ." 
                        required
                        value={password} 
                        onChange={e => setPassword(e.currentTarget.value)}
                    />
                    <input type="submit" value="Log In" />
                </form>
                <div>
                    <button onClick={e => socialLogin(e)} name="googleLogin"> </button>
                </div>
            </div>
        </div>
     }</>
        
    )
}

export default LoginPage
