import React, {useState, useEffect} from 'react'
import { authService } from '../tools/fbase'
import { firebaseInstance } from '../tools/fbase'
import { dbService } from '../tools/fbase'

function RegisterPage({history}) {
    const [id, setId] = useState("");
    const [password, setPassword] = useState("");
    const [done, setDone] = useState(false);
    const [name, setName] = useState("");
    const [job, setJob] = useState("");

    const socialLogin = async (e) => {
        const { 
            target : { name }, 
        } = e;
        let provider;
        if (name === "googleLogin"){
            provider = new firebaseInstance.auth.GoogleAuthProvider();
        }else{
            console.log("   .");
        }

        const data = await authService.signInWithPopup(provider);
        await checkRegister(data.user.multiFactor.user.email);
        setDone(true);

        console.log(data.user.multiFactor.user, "");
    }

    const submit = async (e) => {
        var data = '';
        e.preventDefault();
        try{
            data = await authService.signInWithEmailAndPassword(
                id, password
            )
        } catch (err){
            console.log(err)
        }
        console.log(data)
    }

    const checkRegister = async (email) => {
        const usersCollections = await dbService
            .collection("users")
            .where("email", "==", email)
            .get();
        
        const usersExist = usersCollections.docs.map(doc => {
            return({...doc.data(), id: doc.id})
        })

        if(usersExist.length === 0){
            //       
            console.log("  .");
        }
    }

    const submitInfo = async () => {
        await dbService.collection("users").add({
            name:name,
            job:job,
        })
        alert(" .");
        history.push('/');
        history.go();
    }

    return (
        <>
        {done ? 
        
        <div className="login-container">
            <div className="login__inner">
                <div>
                        .
                </div>
                <form className="form-container" onSubmit={e => submitInfo(e)}>
                    <input 
                        type="text" 
                        placeholder=" ." 
                        required
                        value={name}   
                        onChange={e => setName(e.currentTarget.value)}
                    />
                    <input  
                        type="text" 
                        placeholder=" ." 
                        required
                        value={job} 
                        onChange={e => setJob(e.currentTarget.value)}
                    />
                    <input type="submit" value=" " />
                </form>
            </div>
        </div>

        : 
        <div className="login-container">
            <div className="login__inner">
                <div>
                        .
                </div>
                <form className="form-container" onSubmit={e => submit(e)}>
                    <input 
                        type="text" 
                        placeholder=" ." 
                        required
                        value={id}   
                        onChange={e => setId(e.currentTarget.value)}
                    />
                    <input  
                        type="password" 
                        placeholder=" ." 
                        required
                        value={password} 
                        onChange={e => setPassword(e.currentTarget.value)}
                    />
                    <input type="submit" value="" />
                </form>
                <div>
                    <button onClick={e => socialLogin(e)} name="googleLogin"> </button>
                </div>
            </div>
        </div>}
        </>
    )
}

export default RegisterPage
